<template>
  <v-container class="custom-container">
    <v-row>
      <v-col class="m-1">
         <v-select
          v-model="numberOfCompanions"
          :items="[1, 2, 3, 4, 5, 6]"
          label="동행자 수"
          @update:modelValue="clickComNumber"
          :disabled="textDisabled"
        ></v-select>
      </v-col>
      <v-col v-for="(companion, index) in companions" :key="index" class="m-1">
         <v-text-field 
            outlined class="gray-text-field"
            :label="`동행자 ${index + 1}`"
            :rules="CompanionNameRules"
            :disabled="textDisabled">
        </v-text-field>
      </v-col>
      <template v-if="companionEdit">
        <v-col class="m-1">
            <v-btn @click="companionApply" class="button-style">적용</v-btn>
        </v-col>
      </template>
      <template v-else>
        <v-col class="m-1">
            <v-row class="button-margin">
                <v-btn @click="companionModify" class="button-style">동행자 수정</v-btn>
                <v-btn @click="allReset" class="button-style">전체 초기화</v-btn>
            </v-row>
        </v-col>
      </template>
      <v-col class="m-7"></v-col>
    </v-row>
    
    <!-- 밑줄 -->
    <v-row class="underline-row">
      <v-col>
        <v-divider></v-divider>
      </v-col>
    </v-row>
    <v-row class="overlay" v-if="isOverlayVisible" @click="hideOverlay">
        <!-- 불투명한 영역 -->
    </v-row>
    <v-row class="row-width" v-if="contentFlag" >
        <v-col>
            <div>
                <span class="subtitle-detail">공통 비용</span>
            </div>
        </v-col>
        <v-col/><v-col/><v-col/><v-col/><v-col/><v-col/><v-col/>
        <v-col style="width: 80px; text-align: right;">
            <v-btn @click="costModify" class="button-style">비용 수정</v-btn>
        </v-col>
        <v-table
            fixed-header
            height="300px"
            style="width: 100%"
        >
            <thead>
                <tr>
                    <th class="text-center">NO.</th>
                    <th class="text-center">사용처</th>
                    <th class="text-center">비용(원)</th>
                    <th class="text-center">비고</th>
                </tr>
            </thead>
            <tbody>
            <tr
                v-for="item in publicCosts"
                :key="item.name"
            >
                <td class="text-center">{{ item.costOrder }}</td>
                <td class="text-center">{{ item.costUse }}</td>
                <td class="text-center">{{ item.cost }}</td>
                <td>{{ item.costNote }}</td>
            </tr>
            </tbody>
        </v-table>
    </v-row>
  </v-container>
</template>


<script>
export default {
  data() {
    return {
        numberOfCompanions: 3, // 동행자 수
        companions: Array(3).fill(''), // 각 동행자의 이름을 담을 배열
        isOverlayVisible: true, // 불투명한 영역의 표시 여부
        textDisabled: false,
        companionEdit: true,
        contentFlag: false,
        CompanionNameRules:[
          v => !!v || '동행자 이름은 필수사항 입니다.',
          v => /^[가-힣a-zA-Z\s]*$/.test(v) || '동행자 이름은 한글/영어만 입력 가능합니다.',
          v => !( v && v.length > 15) || '동행자 이름은 8자 이상 입력할 수 없습니다.',
          // v => this.checkCompanion(v)
        ],
        publicCosts: [
            {   costOrder: '1',
                costUse: '해운대개미집',
                cost: '30000',
                costNote: '서비스왕창집',
            },
        ]
    };
  },
  methods: {
    companionApply() {
      this.textDisabled = true;
      this.contentFlag = true;
      this.isOverlayVisible = false;
      this.companionEdit= false;
      
    },
    companionModify(){
      this.textDisabled = false;
      this.contentFlag = false;
      this.isOverlayVisible = true;
      this.companionEdit= true;
    },
    clickComNumber(){
        console.log(this.numberOfCompanions)
        this.companions = Array(this.numberOfCompanions).fill('');
    },
    hideOverlay() {
    // 불투명한 영역을 감추는 메서드
    // this.isOverlayVisible = false;

    
  },

  },
};
</script>

<style scoped>

.custom-container {
  width: 80%;
}

.subtitle-detail {
  margin-top: -50px;
  color: #333;
  font-size: 17px;
  font-weight: bold;
}

.button-style{
   background-color: #333;
   color: #fff;
   font-size: 13px;
   margin-left: 5px;
   margin-top: 10px; /* 적용 버튼을 조금 아래로 이동 */
}

/* v-text-field의 간격을 좁게 조절하는 부분 */
.v-text-field{
    width: 100%; /* 100%로 변경 */
}

.underline-row{
    margin-top: -20px;
}

.overlay {
  position: fixed;
  width: 78%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* 불투명한 배경 색상 */
  z-index: 999; /* 다른 컴포넌트 위에 표시되도록 설정 */
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: not-allowed; /* 클릭을 막음 */
}

.row-width{
  /* width: 100%; 전체 화면의 80%로 조절 */
}

.button-margin{
    margin-top: 3px;
}


</style>